html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
  line-height: 1; }

body {
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica Neue, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol, Noto Color Emoji !important; }

img {
  vertical-align: middle; }

ol,
ul {
  list-style: none;
  margin: 0;
  padding: 0; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

a {
  text-decoration: none; }

.wrae {
  max-width: 1024px;
  margin: 0 auto; }
  .wrae .header-img {
    min-height: 380px;
    max-width: 100%;
    background-image: url(https://i.imgur.com/i4GvwRh.png);
    background-size: cover;
    background-position: center center; }
    .wrae .header-img .header-title {
      padding-top: 50px; }
      .wrae .header-img .header-title h1 {
        font-family: PingFangTC-Regular;
        font-size: 40px;
        color: #ffffff;
        letter-spacing: 3.89px; }
    .wrae .header-img .manu {
      margin-top: 20px;
      position: relative;
      text-align: center; }
      .wrae .header-img .manu .area {
        margin-top: 80px;
        max-width: 452px;
        height: 30px;
        background: rgba(255, 255, 255, 0.7);
        border: 3px solid #ffffff;
        border-radius: 3px;
        text-align: center; }

.fire-top ul {
  max-width: 800px;
  min-height: 112px;
  background: #ffffff;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  margin: 0 auto;
  margin-top: -50px; }
  .fire-top ul h3 {
    font-family: AppleSystemUIFont;
    font-size: 18px;
    color: #000000;
    padding: 20px 0 0 20px; }
  .fire-top ul li {
    text-align: center;
    width: 130px;
    height: 42px;
    margin-left: 90px;
    margin-top: 10px;
    cursor: pointer; }
    .fire-top ul li:before {
      content: "";
      display: inline-block;
      height: 100%;
      width: 0;
      vertical-align: middle; }
    @media (max-width: 425px) {
      .fire-top ul li {
        margin-top: 20px;
        margin-left: 0px; } }

.content {
  margin: auto;
  width: 100%;
  height: 100%; }
  .content .content-title {
    font-family: SFNSDisplay;
    font-size: 36px;
    color: #8a82cc;
    text-align: center; }
  .content .content-ul {
    max-width: 100%;
    min-height: auto;
    text-overflow: ellipsis; }
    .content .content-ul .content-li {
      background: #ffffff;
      box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.2);
      float: left;
      width: 46%;
      margin-left: 2%;
      margin-right: 2%;
      box-sizing: border-box;
      margin-top: 20px;
      border-radius: 10px; }
      @media (max-width: 425px) {
        .content .content-ul .content-li {
          float: none;
          width: 96%; } }
      .content .content-ul .content-li:hover {
        box-shadow: 5px 5px 60px rgba(0, 0, 0, 0.4);
        transform: translate(-5px, -5px); }
      .content .content-ul .content-li .land-img {
        max-width: 500px;
        min-height: 155px;
        background-size: cover;
        background-position: center center;
        position: relative;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px; }
        .content .content-ul .content-li .land-img .land-text {
          width: 100%;
          background-image: linear-gradient(-180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.54) 100%); }
          .content .content-ul .content-li .land-img .land-text h3 {
            font-family: SFNSDisplay;
            font-size: 24px;
            color: #ffffff;
            background-color: rgba(0, 0, 0, 0.8);
            border-radius: 5px;
            position: absolute;
            left: 10px;
            bottom: 0; }
          .content .content-ul .content-li .land-img .land-text h5 {
            font-family: PingFangTC-Regular;
            font-size: 16px;
            color: #ffffff;
            position: absolute;
            background-color: rgba(0, 0, 0, 0.8);
            border-radius: 5px;
            bottom: 0;
            right: 10px; }
      .content .content-ul .content-li .land-info li {
        padding: 10px 10px 10px 10px; }

.footer {
  max-width: 100%;
  height: 90px;
  margin: 0 auto;
  background-color: #559ac8;
  clear: both;
  text-align: center;
  font-family: AppleSystemUIFont; }
  .footer p {
    font-size: 14px;
    color: #ffffff;
    padding-top: 24px; }
  .footer a {
    font-size: 14px;
    color: #ffd366; }

#map {
  height: 400px;
  max-width: 600px;
  margin-top: 20px;
  border-radius: 10px; }
